.App {
  text-align: center;
  font-family: 'Grenze Gotisch', cursive;
}

/*-- Menu Style Start --*/

.Outer-menu{
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  background-image: url("./Photos/gallery_img-04.jpg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  height:72px;
}

.Menu-style {
  color: rgb(31, 29, 29);
  background-color: rgba(44, 2, 2, 0.801);
  -webkit-box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
          box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
  overflow: hidden;
  display: inline-block;
  width: 100%;
  top: 0;
  position: -webkit-sticky;
  position: sticky;
  font-family: 'Patrick Hand', cursive;
  padding-bottom: 5px;
  padding-left:30%;
}


.Menu-style a {
  float: left;
  color: #faf8f5;
  text-align: center;
  padding:15px;
  text-decoration: none;
  font-size: 22px;
  margin-right:20px;
  border-width: 0px;

  border-color: rgba(71, 63, 63, 0);
  border-style: solid;
  border-width: 4px;
  border-top:white;
  border-left: white;
  border-right: white;
}

.Menu-style a:hover {
  color: rgb(229, 255, 0);
  border-color: rgb(229, 255, 0);
}

.menu{
  width: 25px;
  height: 3px;
  background-color: black;
  margin: 4px 0;
}

.btn-light{
  margin:15px 15px;
  display: none;
}

.topnav {
  margin-top:-9px;
  overflow: hidden;
  position: relative;
  text-align: left;
  font-family: 'Patrick Hand', cursive;
  margin-bottom: 9px;
  background-color: rgb(215, 221, 221);
}

.topnav #myLinks {
  display: inline-block;
  width:100%;
  background-color: rgb(215, 221, 221);
}

.topnav a {
  color: brown;
  padding: 9px 45px;
  text-decoration: none;
  font-size: 22px;
  display: block;
 
}


.topnav a:hover {
  color: bisque;
  border-style: solid;
  border-width: 1px;
  border-left: white;
  border-right: white;
  background-color: brown;
}

.Logo{
  height:50px;
  margin-top: 8px;
}

#SiteTitle{
  padding-left: 50px;
  margin-bottom:15px;
}

/*-- Menu End--*/

