@media only screen and (max-width: 1026px) {
  .btn-light{
    display:block;
    background-color: #eeb66d;
    color: white;
  }
  
  .Menu-style {
    margin-bottom: 0px;
  }
  .Menu-style a{
    display:none;
  }
  .Menu-style a:hover{
    display:none;
  }
  .AboutText{
    margin-left:5%;
  }
  .ContactDetails{
    width: 35%;
    border-right: 0px;
  }
}

@media only screen and (max-width: 426px) {
  .HeadText{
    font-size: 50px;
    word-spacing: 10px;
  }
  .Header2{
    padding-top: 170px;
  }
  .HeaderMain{
    margin-left:0%;
    margin-right:0%;
  }
  .AboutMain{
    -ms-flex-direction: column-reverse;
        flex-direction: column-reverse;
  }
  .content1 {
    padding-top: 0px;
    padding-bottom: 70px;
  }
  .SkillBgColour h2{
    padding-top: 70px;
    padding-bottom: 50px;
  }
}

@media only screen and (max-width: 426px) {
  .AboutMain img{
    width: 285px;
    margin-top: 80px;
    margin-right: 0px;
  }
  .AboutText{
    margin-top: 50px;
    margin-left: 0px;
    margin-right: 0px;
    border-radius: 0px;
  }
}

@media only screen and (max-width: 769px) {
  .AboutMain img{
    width: 285px;
  }
  .ContactDetails{
    border-bottom: 1px solid white;
    width: 100%;
    padding: 30px 0px;
  }
}

@media only screen and (max-width: 376px) {
  .Project {
    margin: 20px 10px;
    border-radius: 10px;
  }
  .btn-outline-dark{
    padding: 12px 10px;
  }
}




@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}




@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}



